.skills {
    margin: 10px;
    color: white;
}

.skills__legend {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.legend-item {
    padding: 5px 10px 5px 0px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.legend-text {
    margin-left: 5px;
}

.fa-circle {
    margin: 10px 0 10px 0;
    -webkit-text-stroke: white;
    -webkit-text-stroke-width: 1px;
}

.bg-white {
    background-color: white;
}

.legend-white {
    color: white;
}

.bg-yellow {
    background-color: #ffeb3b;
}

.legend-yellow {
    color: #ffeb3b;
}

