.contact {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    color: white;

    background: #2a2a2a;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

.contact-title {
    padding: 10px;
    margin-bottom: 5px;
    background: #494949;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.5);
}

.contact-options {
    display: flex;
}

.contact-options a{
    color: white
}

.contact-btn {
    width: 100%;
    color: white !important;
    border: transparent solid 1px;
    background: transparent;
}


.contact-btn:hover {
    cursor: pointer;
    background: #494949;
    transition: 200ms;
    border: white solid 1px;
}

.contact__option-email, 
.contact__option-linkedin, 
.contact__option-github {
    flex: 1;
    margin: 10px;
}

.contact-options .fa {
    font-size: 10rem;
}

@media (max-width: 768px) {
    .contact-options {    
        flex-direction: column;
    }
}

@media (min-width: 769px) {
    .contact-options {    
        flex-direction: row;
    }
}